import { useEffect, useState } from "react";

export const Ex = () => {
  const [num, setNum] = useState(0);

  //   useEffect(() => {
  //     // console.log("   ");
  //   });

  //   useEffect(() => {
  //     console.log("  ");
  //   }, []);
  //   => (deps)    

  useEffect(() => {
    console.log("num   ");
    return () => console.log("cleanup  ");
  }, [num]);
  //   =>    useEffect   
  //    :     .

  return (
    <div>
      <h3>{num}</h3>
      <button onClick={() => setNum(num + 1)}>+</button>
      <button onClick={() => setNum(num - 1)}>-</button>
    </div>
  );
};

//   
// *useEffect
// =>  , , ,      
// =>mount :  
// =>update:  
// =>unmount:  
// =>:     
// =>deps:       
// =>cleanup:       
